// import react from "react";
import Input from "../Input";
import { useState } from "react";

import "./Search.css";

const Search = ({ products, productSelected }) => {
  const selectedProduct = (product) => {
    productSelected(product.target.getAttribute("data-number"));
  };

  let [filteredProducts, setFilteredProducts] = useState(products);
  const [inputValue, setInputValue] = useState({
    value: "",
  });

  const userInput = (input) => {
    setInputValue({
      value: input.value,
    });


    // for(let i=0; i< products.length; i++) {
    //   console.log(inputValue.value);
    //     if (products[i].name.includes(inputValue.value)) {
    //       console.log(products[i]);
    //       filteredProducts.push(products[i])
    //     }
    // }
    setFilteredProducts(products.filter((product) => product.name.includes(inputValue.value)));

    // console.log(inputValue.value);
    console.log(filteredProducts);
  };

  return (
    <>
      <div className="header search">
        <Input
          registration={userInput}
          id="search-bar"
          type="text"
          icon="akar-icons:search"
        >
          Pesquisar
        </Input>
      </div>
      {inputValue.value !== "" && (
        <div className="search-container">
          {filteredProducts.map((product, index) => (
            <div className="home-container-item">
              <div
                className="item-text"
                onClick={selectedProduct}
                data-number={index}
              >
                <img src={product.image} alt="" srcset="" data-number={index} />
                <p data-number={index}>
                  {product.name} {product.brand} {product.type} {product.qt}
                </p>
                <p className="item-price" data-number={index}>
                  R${product.price.toString().replace(".", ",")}
                </p>
              </div>
            </div>
          ))}
        </div>
      )}
    </>
  );
};

export default Search;
